
.em {
  display: inline-block;
  width: 1em;
}
.em_5 {
  display: inline-block;
  width: .5em;
}
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
input,
form {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
body {
  background-color: #f0eff6;
  color: #372f2c;
  font-family: "Microsoft Yahei";
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#account > div {
  margin-top: 20px;
  overflow: hidden;
  padding: 10px 20px;
  background: #fff;
}
#account .self-box .fl {
  border-radius: 50%;
}
#account .self-box .self {
  margin-left: 30px;
}
#account .self-box .self .name {
  margin-top: 20px;
  margin-bottom: 10px;
}
#account .purse .fr {
  color: #999;
}
#account .gif-box {
  padding: 0;
}
#account .gif-box > div {
  padding: 10px 20px;
  overflow: hidden;
}
#account .gif-box .gif-manage {
  border-bottom: 1px solid #ddd;
}
#account .gif-box .gif-notify .fl {
  margin-top: 3px;
}
#account .gif-box .gif-notify .fr {
  padding: 0;
}
#account .gif-box > p {
  padding: 10px 20px 0 20px;
  background: #f0eff6;
}
#account .seller {
  margin-top: 0;
  border-top: 1px solid #ddd;
}



